    left: 0;
    display: -webkit-flex;
    display: flex;
    height: 2.5rem;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}
.logotext{
    margin-left: 8rem;
